<template>
  <section class="intro-header">
    <div class="avatar">
      <img
        class="avatar__image"
        alt="Avatar"
        src="@/assets/img/avatar.jpg"
      >
    </div>
    <div class="intro">
      <h1 class="intro__fullname">
        Nguyễn Lương Huy
      </h1>
      <p>
        Experienced Web Developer with a demonstrated history of working in the information technology and services industry.
        Skilled in Vue.js, Nuxt.js, HTML, CSS-SCSS.
        Interesting in Vue and Vue ecosystem.
      </p>
      <ul class="intro__basic-info">
        <li
          v-for="(item, index) in introItems"
          :key="item.name"
          :title="item.title"
          class="intro__item"
          :class="(item.vibloImgSrc ? 'viblo' : '')"
          @mouseover="handleMouseOver"
          @mouseleave="handleMouseLeave"
          @click="handleRedirect(item.link)"
        >
          <div class="intro__item__icon">
            <span v-if="item.faIcon" class="fa-icon">
              <v-icon :name="item.faIcon" scale="2" />
            </span>
            <span v-else>
              <img :src="item.vibloImgSrc" alt="Viblo">
            </span>
          </div>
          <div
            class="intro__item__description"
            :class="(index == introItems.length - 1 ? 'reverse' : '')"
          >
            {{ item.value }}
          </div>
        </li>
      </ul>
    </div>
  </section>
</template>

<script>
import 'vue-awesome/icons/envelope'
import 'vue-awesome/icons/map-marked-alt'
import 'vue-awesome/icons/mobile-alt'
import 'vue-awesome/icons/brands/google'

export default {
  name: 'IntroHeader',
  data () {
    return {
      introItems: [
        {
          name: 'address',
          title: 'Address',
          value: 'No.53, street 37, Tan Quy, dist.7, Ho Chi Minh',
          faIcon: 'map-marked-alt'
        },
        {
          name: 'email',
          title: 'Email',
          value: 'huy.nguyen.luong96@gmail.com',
          faIcon: 'brands/google'
        },
        {
          name: 'phone',
          title: 'Mobile',
          value: '+8485 865 3568',
          faIcon: 'mobile-alt'
        },
        {
          name: 'viblo',
          title: 'Viblo',
          value: 'Nguyen Luong Huy',
          vibloImgSrc: require('@/assets/img/viblo.png'),
          link: 'https://viblo.asia/u/Creat10n'
        }
      ]
    }
  },
  methods: {
    handleMouseOver (e) {
      const targetItem = e.target
      const items = document.querySelectorAll('.intro .intro__item')

      items.forEach(item => {
        if (item !== targetItem) item.classList.add('intro__item--hidden')
      })

      if (targetItem.classList.contains('viblo')) this.introItems[3].vibloImgSrc = require('@/assets/img/viblo-alt.png')
    },

    handleMouseLeave (e) {
      const targetItem = e.target
      const items = document.querySelectorAll('.intro .intro__item')

      items.forEach(item => {
        if (item !== targetItem) item.classList.remove('intro__item--hidden')
      })

      if (targetItem.classList.contains('viblo')) this.introItems[3].vibloImgSrc = require('@/assets/img/viblo.png')
    },

    handleRedirect (link) {
      if (link) window.open(link, '_blank')
    }
  }
}
</script>
